<template>
  <div>
    <Table stripe border :columns="cols" :data="carriers">
      <template slot-scope="{ row }" slot="operator">
        <Button type="success" @click="showDetail(row.ID)" class="opt-btn">detail</Button>
        <Button type="info" @click="eidtDetail(row)" v-if="isVector" class="opt-btn">edit</Button>
        <Poptip
            confirm
            :transfer="true"
            v-if="isVector"
            title="Are you sure you want to delete this item?"
            @on-ok="deleteOk(row.ID)">
          <Button type="error"  class="opt-btn">delete</Button>
        </Poptip>
      </template>
    </Table>
    <Drawer title="edit"
            width="600"
            :mask-closable="false"
            :closable="false"
            v-model="editFlag">
      <Form>
        <Row :gutter="32">
          <i-col span="12" v-for="(value, name) in editCarr" :key="name">
            <FormItem :label="name" label-position="top">
              <Input v-model="editCarr[name]"/>
            </FormItem>
          </i-col>
        </Row>
      </Form>
      <Divider></Divider>
      <div>
        <Button style="margin-right: 8px" @click="editFlag = false">Cancel</Button>
        <Button type="primary" @click="submitEdit">Submit</Button>
      </div>
    </Drawer>
    <v-loading :show="loading"></v-loading>
  </div>
</template>

<script>
import VLoading from "@/components/public/v-loading";
export default {
  name: "carriersList",
  components: {VLoading},
  data : function (){
    return {
      loading : false,
      carriers : [],
      cols : [],
      currentPage : 0,
      pageSize : 0,
      total : 0,
      editFlag : false,
      editCarr : {},
      editId : ""
    }
  },
  beforeMount() {
    this.initPage();
  },
  methods : {
    submitEdit : function (){
      this.editCarr.id = this.editId;
      var postData = {
        carrier : JSON.stringify(this.editCarr)
      }
      this.loading = true;
      var _this = this;
      this.$axios.post("/snapGene/commitCarrier",postData).then(function (res){
        _this.loading = false;
        _this.editFlag = false;
        console.log(res);
        _this.initPage();
        _this.$Message.success("submit success");
      }).catch(function (res){
        _this.loading = false;
        _this.editFlag = false;
        _this.$Message.error("system error!");
        console.log(res);
      })
    },
    deleteOk :function (id){
      console.log(id);
      this.loading = true;
      var postData = {
        fileId : id
      };
      var _this = this;
      this.$axios.post("/snapGene/deleteCarrier", postData).then(function (res){
        console.log(res);
        _this.loading = false;
        _this.$Message.success("delete success");
      }).catch(function (res){
        _this.loading = false;
        console.log(res);
      })
    },
    eidtDetail : function (row){
      this.editId = row.ID;
      for (var name in row){
        if (name == "_id" || name == "ID" || name == "_index" || name == "_rowKey" || name == "id"){
          continue;
        }
        this.editCarr[name] = row[name];
      }
      this.editFlag = true;
    },
    showDetail : function (id){
      this.$router.push({name:"carrier", query:{id:id}});
    },
    initPage : function (){
      this.initCols();
      var postData = {
        currentPage : this.currentPage,
        pageSize : this.pageSize
      }
      var _this = this;
      this.loading = true;
      this.$axios.post("/home/initPage", postData).then(function (res){
        _this.loading = false;
        _this.carriers = res.data.retMap.carriers;
        _this.total = res.data.retMap.total;
      }).catch(function (res){
        _this.loading = false;
        console.log(res);
      })
    },
    initCols :function (){
      var cols = new Array();
      cols.push({
        title : this.$t("CAT"),
        key : "CAT"
      });
      cols.push({
        title : this.$t("NAME"),
        key : "NAME"
      })
      cols.push({
        title : this.$t("BACKBONE载体骨架"),
        key : "BACKBONE载体骨架"
      })
      cols.push({
        title : this.$t("PROMOTER"),
        key : "PROMOTER"
      })
      cols.push({
        title : this.$t("EXPRESSIONCASSETTE表达框"),
        key : "EXPRESSIONCASSETTE表达框"
      })
      cols.push({
        title : this.$t("operator"),
        slot : "operator"
      })
      this.cols = cols;
    }
  },
  computed : {
    isVector : function (){
      if (this.$store.getters.getName === undefined){
        return false;
      }
      if (this.$store.getters.isVector && this.$store.getters.isReviewer){
        return true;
      }else {
        return false;
      }
    }
  }
}
</script>

<style scoped>
.opt-btn{
  display: inline-block;
  margin: 2px 2px;
}
.demo-drawer-footer{
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  border-top: 1px solid #e8e8e8;
  padding: 10px 16px;
  text-align: right;
  background: #fff;
}
</style>